import {Component} from 'react'
import {connect} from 'redux-zero/react';
import fn from '../../states/fn';
import style from './style.css'

const mapToProps = ({ s , d}) => ({ s, d });

class ThumbnailDnD extends Component {
    constructor(props) {
        super(props)
        this.fileHandler = this.fileHandler.bind(this)
        this.fileDragOver = this.fileDragOver.bind(this)
        this.leave = this.leave.bind(this)
        this.state = {
            over: false
        }
    }

    fileHandler = e => {
        e.stopPropagation()
        e.preventDefault()
        let f = e.dataTransfer.files[0];
        const r = new FileReader()
        r.onload = (function(theFile) {
            return function(e) {
                console.log(e.target.result)
            }
        })(f)

        // Read in the image file as a data URL.
        r.readAsDataURL(f);
    }

    fileDragOver = e => {
        e.stopPropagation()
        e.preventDefault()
        this.setState({ over: true })
        e.dataTransfer.dropEffect = 'copy';
    }
    leave = e => {
        e.stopPropagation()
        e.preventDefault()
        this.setState({ over: false })
    }

    componentDidMount() {
        let t = document.getElementsByClassName(style.r)[0]
        t.addEventListener('dragover', this.fileDragOver, false)
        t.addEventListener('dragleave', this.setState({over: false}), false)
        t.addEventListener('drop', this.fileHandler, false)
    }

    render() {
        return(
            <div onDragLeave={this.leave} className={style.r} style={this.state.over ? {background: '#ccc url(data:image/png;base64,) no-repeat bottom -30px right 20px', backgroundSize: '120px auto'} : {background: '#ebebeb url(data:image/png;base64,) no-repeat bottom -30px right 20px', backgroundSize: '120px auto'}}>
                <div className={style.dnd}>
                        <p>Thumbnail here</p>
                </div>
            </div>
        )
    }
}

export default connect(mapToProps, fn)(ThumbnailDnD)
